import React, { Dispatch, useContext } from 'react';
import styles from './SongItem.module.css';
import { trimString } from '../../helpers/helpers';
import { Howl, Howler } from 'howler';
import { ActionTypes } from '../../store/actions/actionTypes';
import { playSong } from '../../store/actions/actions';
import { connect, ConnectedProps } from 'react-redux';
import { SongContext } from '../../context/songContext';

interface Props extends ReduxProps {
    song: any
}

const SongItem: React.FC<Props> = ({ song, playSong }) => {
    const state = useContext(SongContext);

    Howler.volume(.5);
    /**
     * Create the song variable based on Howl and save to Context data and Redux
     * @param src The source of song file
     */
    const soundPlay = (src: string): void => {
        let sound = new Howl({
            src
        });
        sound.play();

        state?.setSound(sound);

        const currentSong: Song = {
            name: song.album.title,
            artist: song.artist.name,
            smallImage: song.album.cover_small,
            bigImage: song.album.cover_big
        }
        playSong(currentSong);
    };
    
    return (
        <div className={styles.SongItem} onClick={() => soundPlay(song.preview)}>
            <div style={{ backgroundImage: `url(${song.album.cover_medium})`, backgroundSize: 'cover', height: '80%'}}></div>
            <div className={styles.title}>{trimString(song.album.title)}</div>
            <div className={styles.artist}>{song.artist.name}</div>
        </div>
    );
};

const mapDispatchToProps = (dispatch: Dispatch<ActionTypes>) => ({
    playSong: (song: Song) => dispatch(playSong(song))
});

const connector = connect(null, mapDispatchToProps);

type ReduxProps = ConnectedProps<typeof connector>;

export default connector(SongItem);